.teacherImage{
    border-radius: 50%;
    border: 3px solid #00a59f;
    width: 100%;
    max-width: 170px;
    max-height: 170px;
    height: auto;
}
.sponsorImage{
    width: 200px;
    height: 200px;
}
.logo{
    width: 450px;
    height: 450px;
}
@media(max-width: 960px) {
    .logo{
        width: 300px;
        height: 300px;
    }
    .teacherImage{
        border-radius: 50%;
        border: 3px solid #00a59f;
        width: 100%;
        max-width: 120px;
        max-height: 120px;
        height: auto;


    }
    .sponsorImage{
        width: 100px;
        height: 100px;
    }
}
